import React, { Fragment, useState } from 'react';
// import { Modal, Button } from 'react-bootstrap';

const Todo = ({ text, isCompleted, deleted, completed, changed }) => {
    // const [show, setShow] = useState(false);

    // const handleClose = () => setShow(false);
    // const handleShow = () => setShow(true);

    return (
        <Fragment>
            <div
                className={`todo-indicator ${
                    isCompleted ? 'bg-success' : 'bg-warning'
                }`}
            ></div>
            <div className='widget-content p-0'>
                <div className='widget-content-wrapper'>
                    <div className='widget-content-right flex1'>
                        <div className='widget-heading'>
                            {isCompleted ? <del>{text}</del> : text}
                        </div>
                    </div>
                    <div className='widget-content-left'>
                        {' '}
                        <button
                            className='border-0 btn-transition btn btn-outline-success'
                            onClick={completed}
                        >
                            {' '}
                            <i className='fa fa-check' />
                        </button>{' '}
                        <button
                            className='border-0 btn-transition btn btn-outline-danger'
                            onClick={deleted}
                        >
                            {' '}
                            <i className='fa fa-trash' />{' '}
                        </button>{' '}
                        <button
                            variant='outline-primary'
                            className='border-0 btn-transition'
                            // onClick={handleShow}
                        >
                            <i className='fa fa-edit' />
                        </button>
                        {/* <div className='rtl'>
                            <Modal show={show} onHide={handleClose}>
                                <Modal.Header closeButton />
                                <Modal.Body>
                                    <input
                                        type='text'
                                        placeholder={`تغییر ${text}`}
                                        onChange={changed}
                                    />
                                </Modal.Body>
                                <Modal.Footer>
                                    <Button
                                        variant='success'
                                        onClick={handleClose}
                                    >
                                        ذخیره تغییرات
                                    </Button>
                                </Modal.Footer>
                            </Modal>
                        </div> */}
                    </div>
                </div>
            </div>
        </Fragment>
    );
};

export default Todo;
